.navbar{
    display: flex;
    position:fixed;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    height:100vh;
    width:20vw;
    margin-left:5vw;
    background:linear-gradient(to bottom,rgba(233, 146, 16, 0.603),orange);
}

.navbar h1{
    color:blueviolet;
}
.avatar{
    width: 55%;
  height: 25%; 
  border-radius: 50%;
  border-width: 5px;
  border-style:solid;
  border-color: white;
  object-fit:cover;
  transition: 1s all;
}

.avatar:hover{
    border-color:blueviolet;
}

.navbar > .refer_id{
    padding:5px;
    background-color: linear-gradient(to bottom,rgba(233, 146, 16, 0.603),orange);
    margin:5px;
    width:60%;
    border-radius: 5px;
    cursor: pointer;
    color:white;
    font-weight: 300;
    font-size: large;
    transition: 0.5s all;
}

.navbar > div:active{
    background-color: black;
}

 a{
    text-decoration: none;
    outline: none;
    color:white;
    font-weight: bold;
    

}

.icon{
    font-size: large;
    color:blue;
}

.social-links{
    width: 60%;;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
}

.refer_id{
    box-shadow: 0px 2px #eee;
    display: flex;;
    flex-direction: row;
    justify-content:center;
    align-items: center;
}